// src/members/mini-studio/DashboardTable.tsx
import React, { useEffect, useState } from "react";
import { navigate } from "@reach/router";
import Avatar from "../shared/Avatar/Avatar";
import { supabase } from "../../database/supabaseClient";
import { qstyle } from "./dashboard-table";

const DashboardTable: React.FC<{ query: string }> = ({ query }) => {
  const [rows, setRows] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchMembers = async () => {
      console.log("üì° [DashboardTable] Fetching from user_member_v2‚Ä¶");
      setLoading(true);
      setError(null);

      try {
        const { data, error } = await supabase
          .from("user_member_v2")
          .select(`
            id,
            created_at,
            full_name,
            hometown,
            title,
            avatar_url,
            field_of_expertise
          `)
          .order("created_at", { ascending: false });

        if (error) throw error;
        if (!data?.length) {
          console.warn("‚ö†Ô∏è No members found in user_member_v2");
          setRows([]);
          return;
        }

        // üß© 1Ô∏è‚É£ Exclude system/admin accounts
        const excludedIds = [
          "6c77d859-1af1-418e-b0e0-0d3cc4e80fad",
          "02c567a2-b193-4009-89e5-884386540733",
          "e0a7bf63-0d67-4e86-95f2-2e2c3276e7f6",
          "e04e38e0-7eed-4281-bc05-89fb6c9b30f1",
          "387899c3-fa8a-4582-b6dc-0833dfc02f40",
        ];
        const filteredData = data.filter((x) => !excludedIds.includes(x.id));

        console.log(
          `‚úÖ Loaded ${filteredData.length} visible members (filtered from ${data.length})`
        );
        setRows(filteredData);
      } catch (err: any) {
        console.error("üí• Fetch error:", err);
        setError(err.message || "Unknown error");
        setRows([]);
      } finally {
        setLoading(false);
      }
    };

    fetchMembers();
  }, []);

  // üß© 2Ô∏è‚É£ Search / query filter
  const searchFiltered = rows.filter((m) =>
    [m.full_name, m.title, m.field_of_expertise, m.hometown]
      .join(" ")
      .toLowerCase()
      .includes(query.toLowerCase())
  );

  /* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
     Render states
  ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
  if (loading)
    return <div style={{ padding: 40, textAlign: "center" }}>‚è≥ Loading members‚Ä¶</div>;

  if (error)
    return <div style={{ padding: 40, textAlign: "center", color: "red" }}>‚ùå {error}</div>;

  if (!rows.length)
    return (
      <div style={{ padding: 40, textAlign: "center", color: "#888" }}>
        ‚ö†Ô∏è No members found in <code>user_member_v2</code>.
      </div>
    );

  if (!searchFiltered.length)
    return (
      <div style={{ padding: 40, textAlign: "center", color: "#888" }}>
        üîç No results match <code>{query}</code>.
      </div>
    );

  /* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
     Main table
  ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
  return (
    <section style={{ position: "relative" }}>
      <style>{qstyle}</style>

      <div
        className="table-header"
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          padding: "12px 16px",
          borderBottom: "1px solid var(--border)",
        }}
      >
        <div>
          <strong>Members</strong>
          <div style={{ fontSize: "11px", color: "var(--muted)" }}>
            Showing {searchFiltered.length} of {rows.length} total
          </div>
        </div>
      </div>

      <table>
        <thead>
          <tr>
            <th>#</th>
            <th>Avatar</th>
            <th>Full Name</th>
            <th>Title</th>
            <th>Expertise</th>
            <th>Hometown</th>
            <th>Joined</th>
          </tr>
        </thead>
        <tbody>
          {searchFiltered.map((m, i) => (
            <tr key={m.id} onClick={() => navigate(`${m.id}`)}>
              <td>{i + 1}</td>
              <td>
                <div className="avatar-wrapper">
                  <Avatar
                    url={m.avatar_url || ""}
                    width={32}
                    height={32}
                    alt={m.full_name || "User Avatar"}
                  />
                </div>
              </td>
              <td>{m.full_name || "‚Äî"}</td>
              <td>{m.title || "‚Äî"}</td>
              <td>{m.field_of_expertise || "‚Äî"}</td>
              <td>{m.hometown || "‚Äî"}</td>
              <td>
                {m.created_at
                  ? new Date(m.created_at).toLocaleDateString(undefined, {
                      year: "2-digit",
                      month: "2-digit",
                      day: "2-digit",
                    })
                  : "‚Äî"}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </section>
  );
};

export default DashboardTable;
